@import '../base/variables.sass';
@import '../base/mixins.sass';

.large-hero {
    position: relative;
    border-bottom: 0.625rem solid $sec-color;

    &__image {
        display: block; }

    &__text-block {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 100%;
        text-align: center; }


    &__title {
        font-weight: 300;
        color: $sec-color;
        font-size: 2.4rem;
        margin: 0;

        // +at-small
        //     font-size: 2rem

        // +at-medium
        //     font-size: 3.2rem

        @include at-large {
            font-size: 4.8rem; } }


    &__subtitle {
        font-weight: 300;
        color: $sec-color;
        font-size: 1.5rem;
        margin: 0;

        @include at-small {
            font-size: 2.9rem; } }


    &__paragraph {
        color: $accent-one;
        font-size: 1.5rem;
        font-weight: 100;
        text-shadow:  2px 2px 0 rgba(0,0,0,0.1);
        max-width: 30rem;
        margin-left: auto;
        margin-right: auto;

        @include at-small {
            font-size: 1.875rem; } } }



