@import '../base/variables.sass';

.btn {
    background-color: $sec-color;
    color: $accent-one;
    text-decoration: none;
    padding: 0.75rem 1.2rem;
    display: inline-block;

    &--accent-two {
        background-color: $accent-two; }


    &--large {
        font-size: 1.1rem;
        padding: 1.1rem 1.9rem;

        @include at-small {
            font-size: 1.25rem; } } }

